<template>
  <div class="home">
    <h1>메타버스 진행상황</h1>
    <!-- <p>localStorage: {{ a() }}</p> -->
    <ul>
      <li
        class="m-1 p-1 bg-black text-white"
        v-for="(v, k) in lsItems"
        v-bind:key="k"
      >
        {{ k }} - {{ lsGet(k) }}
      </li>
    </ul>
    >

    <p>MODE: {{ b() }}</p>
    <p>USERNAME: {{ $store.getters.userName }}</p>
    <p>API URL: {{ $store.getApiUrl() }}</p>
    <p>GUID: {{ $store.getGUID() }}</p>
    <p>{{ $store.getters.userName }}</p>

    <!-- <iframe
      src="/game1/game.html"
      width="640"
      height="480"
      scrolling="no"
      frameborder="0"
    >
    </iframe> -->
    <!-- <comp-1 /> -->
  </div>
</template>

<script>
// @ is an alias to /src
// import Comp1 from "../components/Comp1.vue";

export default {
  name: "Home",
  // components: {
  //   Comp1,
  // },
  mounted() {
    // document.cookie = "test1=Hello";
    // document.cookie = "test2=World";

    localStorage.setItem("a", {
      key: "a",
      val: 1,
    });
    localStorage.setItem("akey", "avalue");

    // for (let i = 0; i < localStorage.length; i++) {
    //   const k = localStorage.key(i);
    //   const v = localStorage.getItem(k);

    //   console.log("mounted", k, v, typeof v);
    // }
  },
  computed: {
    lsItems() {
      const items = {};
      for (let i = 0; i < localStorage.length; i++) {
        const k = localStorage.key(i);
        const v = localStorage.getItem(k);
        items[k] = v;
      }
      return items;
    },
  },
  methods: {
    a() {
      let text = "";
      for (let i = 0; i < localStorage.length; i++) {
        let key = localStorage.key(i);
        text = `${text} @ ${key}`;
      }
      return text;
    },
    b() {
      return process.env.NODE_ENV;
    },
    lsGet(key) {
      return localStorage.getItem(key);
    },
  },
};
</script>
